/***************************
Followers and Following Container Styling 
**************************/
.followers__container,
.following__container {
  height: 100vh;
  width: 100%;
  font-family: "Poppins", sans-serif;
  display: block;
}
.followers__container h1,
.following__container h1 {
  width: 90%;
  max-width: 640px;
  background-color: var(--color-white);
  box-shadow: 5px 5px 8px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  margin: 3em auto 0;
  text-align: Center;
  font-family: "Nunito", sans-serif;
  text-transform: uppercase;
  font-size: 1.9em;
  padding: 0.4em 0;
  color: var(--h1-color);
}

.followers__wrapper,
.following__wrapper {
  overflow-y: scroll;
  overflow-x: hidden;
  width: 90%;
  max-width: 640px;
  background-color: var(--color-white);
  box-shadow: 5px 5px 8px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  margin: 1em auto 2em;
}

.follower,
.following {
  display: grid;
  grid-template-columns: 0.85fr 1.5fr 1fr;
  justify-content: space-between;
  align-items: flex-start;
  padding: 0.75em 1em;
  border-bottom: 1px solid var(--input-border-color);
}
.follower__avatar__link,
.following__avatar__link {
  justify-self: center;
  width: 3em;
  height: 3em;
  border-radius: 50%;
  display: inline-block;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.55);
  text-decoration: none;
}
.follower__avatar__link img,
.following__avatar__link img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}
.follower__name__wrapper,
.following__name__wrapper {
  justify-self: center;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  font-size: 0.9em;
}
.follower__name__wrapper a,
.following__name__wrapper a {
  text-decoration: none;
  color: var(--color-black);
  display: inline-block;
}
.follower__name__wrapper h4,
.following__name__wrapper h4 {
  color: #555;
  line-height: 1.2;
  font-weight: 400;
}

.follow__btn__container,
.following__btn__container {
  justify-self: center;
}
.follow__btn__container button,
.following__btn__container button {
  background-color: var(--h1-color);
  padding: 0.3em 0.8em;
  border-radius: 0.6em;
  display: block;
  color: var(--color-white);
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
  font-size: 0.85em;
  border: none;
  font-family: inherit;
  font-weight: 500;
}
.follow__btn__container button:hover, .follow__btn__container button:focus,
.following__btn__container button:hover,
.following__btn__container button:focus {
  opacity: 0.75;
  cursor: pointer;
}

.follower:last-child,
.following:last-child {
  border: none;
}